





























































import { Vue, Watch } from 'vue-property-decorator';
import Component from 'vue-class-component';
import { IChapter, getChapters, getChapter, IBook } from '../api/book';
import ReadPager from '@/components/ReadPager.vue'
import BackHeader from '@/components/BackHeader.vue';
import SliderMenu from '@/components/SliderMenu.vue';
import {Range, MessageBox, Toast, Indicator} from 'mint-ui';
import { getLocalStorage } from '@/utils';
import BookRecord,{ ITheme } from '@/utils/book';
import { dispatchChapter, dispatchBook } from '@/store/dispatches';
import { constants } from 'fs';
import { THEME_CONFIGS_KEY } from '@/store/types';

interface IProgress {
    page: number,
    count: number,
    progress: number
}

Vue.component(Range.name, Range);

Component.registerHooks([
  'beforeRouteLeave'
]);

@Component({
    components: {
        ReadPager,
        BackHeader,
        SliderMenu
    },
})
export default class Read extends Vue {

    width: number = 0;
    height: number = 0;
    progress: number = 0;
    chapter?: IChapter = {id: 0};
    book?: IBook;
    readMode: number = 0;
    isReady = false;
    isPagerReady = false;
    theme_list = [0, 1, 2, 3, 4, 5];
    font_list = ['', '', '', ''];
    configs: ITheme = {
        font: 3,
        theme: 0,
        old_theme: 0, // 
        size: 18,
        line: 10,
        letter: 4
    };
    size_round =  {
        size: [12, 2, 40],
        line: [2, 1, 40],
        letter: [1, 1, 40],
    };

    beforeRouteLeave(to: any, from: any, next: Function) {
        if (!this.chapter) {
            next();
            return;
        }
        if (!BookRecord.has(this.chapter.book_id)) {
            MessageBox.confirm('').then(() => {
                this.recordFollow();
                Toast('')
                next()
            }, () => {
                next()
            })
            return;
        }
        this.recordFollow();
        next()
    }
    
    created() {
        dispatchChapter(parseInt(this.$route.params.id)).then(res => {
            this.chapter = res;
            this.refreshPager();
            this.$refs.slider.refresh(res.book_id);
        });
        this.configs = BookRecord.getTheme();
    }

    mounted () {
        this.isReady = true;
        window.onresize = () => {
            this.refreshSize();
        }
        this.refreshSize();
        this.refreshPager();
    }

    recordFollow() {
        if (!this.chapter) {
            return;
        }
        if (BookRecord.has(this.chapter.book_id)) {
            BookRecord.update(this.chapter, this.progress);
            return;
        }
        dispatchBook(this.chapter.book_id).then(book => {
            BookRecord.add(book, this.chapter, this.progress);
        })
    }

    refreshSize() {
        let size = this.getClientSize();
        this.width = size.width;
        this.height = size.height;
    }

    getClientSize() {
        if(window.innerHeight !== undefined){
            return {
                'width': window.innerWidth,
                'height': window.innerHeight
            }
        }
        if(document.compatMode === "CSS1Compat"){
            return {
                'width': document.documentElement.clientWidth,
                'height': document.documentElement.clientHeight
            }
        }
        return {
            'width': document.body.clientWidth,
            'height': document.body.clientHeight
        }
    }

    refreshPager(page: number = 1) {
        if (!this.isReady || this.isPagerReady) {
            return;
        }
        if (!this.chapter || this.chapter.id < 1) {
            return;
        }
        this.isPagerReady = true;
        this.$route.meta.title = this.chapter.title;
        this.$refs.pager.refreshPager(this.chapter.content);
        this.goPager(page);
    }

    tapPrev() {
        if (!this.chapter || !this.chapter.previous) {
            Toast('');
            return;
        }
        Indicator.open('' + this.chapter.previous.title + '');
        getChapter(this.chapter.previous.id).then(res => {
            Indicator.close();
            this.chapter = res;
            this.isPagerReady = false;
            this.refreshPager();
        });
    }

    tapRead(item: IChapter) {
        this.readMode = 0;
        if (this.chapter && item.id == this.chapter.id) {
            return;
        }
        this.chapter = item;
        Indicator.open('' + item.title + '');
        getChapter(item.id).then(res => {
            Indicator.close();
            this.chapter = res;
            this.isPagerReady = false;
            this.refreshPager();
        });
    }

    tapNext() {
       if (!this.chapter || !this.chapter.next) {
            Toast('');
            return;
        }
        Indicator.open('' + this.chapter.next.title + '');
        getChapter(this.chapter.next.id).then(res => {
            Indicator.close();
            this.chapter = res;
            this.isPagerReady = false;
            this.refreshPager();
        });
    }

    tapProgress(data: IProgress) {
        this.progress = data.progress;
    }

    goPager(page: number) {
        this.$refs.pager.goPager(page);
    }

    tapMoveProgress(val: number) {
        this.progress = val;
        this.$refs.pager.goProgress(val)
    }

    tapMiddle(toggle?: boolean) {
        if (typeof toggle == 'undefined') {
            this.readMode = this.readMode > 0 ? 0 : 1;
            return;
        }
        if (this.readMode > 0) {
            this.readMode = 0;
        }
    }

    tapSetting() {
        this.readMode = 2;
    }

    tapEye() {
        if (this.configs.theme == 7) {
            this.configs.theme = this.configs.old_theme;
            return;
        }
        this.configs.old_theme = this.configs.theme;
        this.configs.theme = 7;
    }

    tapMinus(name: string) {
        if (!this.configs.hasOwnProperty(name)) {
            return;
        }
        const round = {
            size: [12, 2, 40],
            line: [2, 1, 40],
            letter: [1, 1, 40],
        };
        this.configs[name] = Math.min(Math.max(this.configs[name] - this.size_round[name][1], this.size_round[name][0]), this.size_round[name][2]);
        this.$refs.pager.refreshPage();
    }

    tapPlus(name: string) {
        if (!this.configs.hasOwnProperty(name)) {
            return;
        }
        this.configs[name] = Math.min(Math.max(this.configs[name] + this.size_round[name][1], this.size_round[name][0]), this.size_round[name][2]);
        this.$refs.pager.refreshPage();
    }

    @Watch('configs', {deep: true})
    onThemeChange(val: ITheme, old: any) {
        BookRecord.saveTheme(this.configs);
    }

    tapChapter() {
        this.readMode = 4;
    }
}
