import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home.vue'
import Category from '@/pages/Category.vue'
import Search from '@/pages/Search.vue'
import Book from '@/pages/Book.vue'
import Chapter from '@/pages/Chapter.vue'
import Read from '@/pages/Read.vue'
import Bang from '@/pages/Bang.vue'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home,
            meta: {
                title: '',
            },
        },
        {
            path: '/category',
            name: 'category',
            component: Category,
            meta: {
                title: '',
            },
        },
        {
            path: '/bang',
            name: 'bang',
            component: Bang,
            meta: {
                title: '',
            },
        },
        {
            path: '/search',
            name: 'search',
            component: Search,
            meta: {
                title: '',
            },
        },
        {
            path: '/book/:id',
            name: 'book',
            component: Book,
            meta: {
                title: '',
            },
        },
        {
            path: '/chapter/:book',
            name: 'chapter',
            component: Chapter,
            meta: {
                title: '',
            },
        },
        {
            path: '/read/:id',
            name: 'read',
            component: Read,
            meta: {
                title: '',
            },
        },
    ],
})
