import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Http from './utils/http'
import Title from './utils/title'

import { assetsFilter, statusFilter, sizeFilter, agoFilter } from './pipes'
import { getSessionStorage } from './utils';
import { TOKEN_KEY } from './store/types';

import '@fortawesome/fontawesome-free/css/all.css';

Vue.filter('assets', assetsFilter);
Vue.filter('status', statusFilter);
Vue.filter('size', sizeFilter);
Vue.filter('ago', agoFilter);
Vue.use(Http)
Vue.use(Title)

router.beforeEach((to, from, next) => {
    const token = getSessionStorage<string>(TOKEN_KEY) // token
    if (to.meta.requireAuth) { // 
        if (token && token.length > 0) { // vuex statetoken
            next()
        } else {
            next({
                path: '/login',
                query: {
                    redirect: to.fullPath,
                }, // path
            })
        }
    } else {
        next()
    }
})

new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount('#app');
