import axios from 'axios';
import router from '@/router'
import { Toast } from 'mint-ui'
import { Md5 } from 'ts-md5';
import * as util from './'
import { TOKEN_KEY } from '@/store/types'

axios.defaults.timeout = 60000
axios.defaults.baseURL = util.apiEndpoint

// http request 
axios.interceptors.request.use(
    (config) => {
        // const token = getCookie('');cookiejs-cookie
        config.data = JSON.stringify(config.data)
        config.headers = {
            'Content-Type': 'application/vnd.api+json',
            'Accept': 'application/json',
        }
        const timestamp = util.getCurrentTime()
        const sign = Md5.hashStr(util.appId + timestamp + util.secret)
        if (!config.params) {
            config.params = {}
        }
        config.params.appid = util.appId;
        config.params.timestamp = timestamp;
        config.params.sign = sign;
        const token = util.getSessionStorage(TOKEN_KEY)
        if (token) {
            config.params.token = token
        }
        return config
    },
    (error) => {
        return Promise.reject(error)
    },
)

// http response 
axios.interceptors.response.use(
    (response) => {
        if (response.data.errCode === 2) {
            router.push({
                path: '/login',
                query: {
                    redirect: router.currentRoute.fullPath,
                }, // 
            })
            Toast({
                message: response.data.Message,
                position: 'bottom',
            })
        }
        return response
    },
    (error) => {
        Toast({
            message: error,
            position: 'bottom',
        })
        return Promise.reject(error)
    },
)

/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */
export function fetch<T>(url: string, params = {}): Promise<T> {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params,
        }).then((response) => {
            resolve(response.data)
        }).catch((err) => {
            reject(err)
        })
    })
}

/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */
export function post(url: string, data = {}) {
    return new Promise((resolve, reject) => {
        axios.post(url, data)
            .then((response) => {
                resolve(response.data)
            }, (err) => {
                reject(err)
            })
    })
}

/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */
export function patch(url: string, data = {}) {
    return new Promise((resolve, reject) => {
        axios.patch(url, data)
            .then((response) => {
                resolve(response.data)
            }, (err) => {
                reject(err)
            })
    })
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */
export function put(url: string, data = {}) {
    return new Promise((resolve, reject) => {
        axios.put(url, data)
            .then((response) => {
                resolve(response.data)
            }, (err) => {
                reject(err)
            })
    })
}

export default {
    install(Vue: any) {
        Vue.prototype.$post = post
        Vue.prototype.$fetch = fetch
        Vue.prototype.$patch = patch
        Vue.prototype.$put = put
    },
}
