











import Vue from 'vue'
export default Vue.extend({
      data() {
        return {
        };
    },
    props: {
        title: String,
    },
    methods: {
        tapBack() {
            if (window.history.length <= 1) {
                this.$router.push('/');
                return;
            }
            this.$router.go(-1);
        },
    }
})
