








import Vue from 'vue';

interface IMenu {
    name: string,
    icon: string,
    url: string
}

export default Vue.extend({
    name: 'TabBar',
    data() {
        return {
            menus: [
                {
                    name: '',
                    icon: 'fa fa-book',
                    url: 'home',
                },
                {
                    name: '',
                    icon: 'fa fa-th-large',
                    url: 'category',
                },
                {
                    name: '',
                    icon: 'fa fa-search',
                    url: 'search',
                },
                {
                    name: '',
                    icon: 'fa fa-user',
                    url: 'member',
                },
            ],
        };
    },
    methods: {
        tapMenu(item: IMenu) {
            this.$router.push({ name: item.url});
        },
    },
})
