















import Vue from 'vue';
import { Pager } from '@/utils/pager';
export default Vue.extend({
    data() {
        let pager: Pager = new Pager('');
        return {
            pager,
            page: 1,
            pageCount: 0,
            startX: 0,       //  
            isSilde: false,  // 
            isRight: 0       //  0  1  2  3 
        };
    },
    props: {
        width: {
            type: Number,
            default: 0
        },
        height: {
            type: Number,
            default: 0
        },
        fontsize: {
            type: Number,
            default: 18
        }, 
        linespace: {
            type: Number,
            default: 10
        },
        letterspace: {
            type: Number,
            default: 4
        },
        left: {
            type: Number,
            default: 10
        },
        top: {
            type: Number,
            default: 10
        },
        color: {
            type: String,
            default: '#333'
        },
        font: String,
        theme: String
    },
    methods: {
        getWidth() {
            if (this.width < 1) {
                return this.$parent.width;
            }
            return this.width;
        },
        getHeight() {
            if (this.height < 1) {
                return this.$parent.height;
            }
            return this.height;
        },
        isToRight() {
            return this.isRight === 1;
        },
        isToLeft() {
            return this.isRight === 2;
        },
        refreshPager(text: string) {
            this.pager = new Pager(text);
        },
        tapIfClick(x: number, y: number) {
            const centerX = this.getWidth() / 2;
            if (Math.abs(centerX  - x) < 50 && Math.abs(this.getHeight() / 2 - y) < 50) {
                // 
                this.$emit('middle');
                return;
            }
            this.$emit('middle', false);
            if (centerX > x) {
                // 
                this.goPrev();
                return;
            }
            this.goNext();
        },
        touchstart(e: TouchEvent) {
            this.startX = e.targetTouches[0].clientX;
            this.isRight = 0;
            this.isSilde = false;
        },
        touchmove(e: TouchEvent) {
            this.isSilde = true;
            this.$emit('middle', false);
            if (this.isRight === 3) {
                return;
            }
            const diff = e.targetTouches[0].clientX - this.startX;
            if (this.isRight === 0) {
                this.isRight = diff < 0 ? 1 : 2;
                if (!this.readyPage()) {
                    return;
                }
            } else if (
                (this.isToRight() && diff > 0) 
                || (this.isToLeft() && diff < 0)
            ) {
                return;
            }
            if (Math.abs(diff) > this.getWidth()) {
                this.isToRight() ? this.applyNext() : this.applyPrev();
                return;
            }
            if (this.isToRight()) {
                this.$refs.currentPage.style.left = diff + 'px';
                return;
            }
            this.$refs.prevPage.style.left = diff - this.getWidth() + 'px';
        },
        touchend(e: TouchEvent) {
            if (!this.isSilde) {
                this.tapIfClick(this.startX, e.changedTouches[0].clientY);
                return;
            }
            if (this.isRight !== 1 && this.isRight !== 2) {
                return;
            }
            const diff = e.changedTouches[0].clientX - this.startX;
            if (
                (this.isToRight() && diff > 0) 
                || (this.isToLeft() && diff < 0)
            ) {
                return;
            }
            if (this.isToRight()) {
                this.animation(this.$refs.currentPage, diff, - this.getWidth(), () => {
                    this.applyNext();
                });
                return;
            }
            this.animation(this.$refs.prevPage, diff - this.getWidth(), 0, () => {
                this.applyPrev();
            });
        },
        /**
         * 
         */
        readyPage() {
            if (this.isToRight()) {
                return this.readyNext();
            }
            return this.readyPrev();
        },
        /**
         * 
         */
        readyPrev() {
            if (this.page < 2) {
                this.isRight = 3;
                this.$emit('prev');
                return false;
            }
            this.applyPage(this.$refs.prevPage, this.page - 1);
            return true;
        },
        /**
         * 
         */
        readyNext() {
            if (this.page >= this.pageCount) {
                this.$emit('next');
                return false;
            }
            this.applyPage(this.$refs.nextPage, this.page + 1);
            return true;
        },
        /**
         * 
         */
        applyNext() {
            this.page ++;
            this.swapHtml(this.$refs.nextPage, this.$refs.currentPage);
            this.$refs.currentPage.style.left = '0px';
            this.isRight = 3;
            this.notifyProgress();
        },
        /**
         * 
         */
        applyPrev() {
            this.page --;
            this.swapHtml(this.$refs.prevPage, this.$refs.currentPage);
            this.$refs.prevPage.style.left = - this.getWidth() * 1.1 +'px';
            this.isRight = 3;
            this.notifyProgress();
        },
        /**
         * 
         */
        animation(
            element: HTMLDivElement, start: number, end: number, endHandle: Function) {
            const diff = start > end ? -1 : 1;
            let step = 1;
            let handle = setInterval(() => {
                start += (step ++) * diff;
                if ((diff > 0 && start >= end) || (diff < 0 && start <= end)) {
                    clearInterval(handle);
                    element.style.left = end + 'px';
                    endHandle();
                    return;
                }
                element.style.left = start + 'px';
            }, 16);
        },
        /**
         * 
         */
        swapHtml(src: HTMLDivElement, dist: HTMLDivElement) {
            for (const item of dist.children) {
                dist.removeChild(item);
            }
            for (const item of src.children) {
                dist.appendChild(item);
            }
        },
        /**
         * 
         */
        applyPage(element: HTMLDivElement, page: number) {
            element.innerHTML = this.pager.toHtml(page, this.fontsize, this.linespace, this.letterspace, this.getWidth() - 2 * this.left, this.getHeight() - 2* this.top, this.left, this.top);
        },
        /**
         * 
         */
        refreshPage() {
            const old_count = this.pageCount;
            this.pageCount = this.pager.getPageCountWithSize(this.fontsize, this.linespace, this.letterspace, this.getWidth() - 2 * this.left, this.getHeight() - 2* this.top);
            if (old_count > 0 && this.pageCount !== old_count) {
                this.page = Math.floor(this.page * this.pageCount / old_count); // 
                this.notifyProgress();
            }
            if (this.page < 1) {
                this.page = 1;
            } else if (this.pageCount > 0 && this.page > this.pageCount) {
                this.page = this.pageCount;
            }
            this.applyPage(this.$refs.currentPage, this.page);
        },
        /**
         * 
         */
        notifyProgress() {
            this.$emit('progress', {
                page: this.page,
                count: this.pageCount,
                progress: Math.ceil(this.page * 100 / this.pageCount)
            });
        },
        /**
         * 
         */
        goPager(page: number) {
            this.page = page;
            this.refreshPage();
        },
        /**
         * 
         */
        goProgress(progress: number) {
            this.goPager(Math.floor(progress * this.pageCount / 100));
        },
        /**
         * 
         */
        goPrev() {
            if (!this.readyPrev()) {
                return false;
            }
            this.animation(this.$refs.prevPage, -this.getWidth(), 0, () => {
                this.applyPrev();
            });
        },
        /**
         * 
         */
        goNext() {
            if (!this.readyNext()) {
                return false;
            }
            this.animation(this.$refs.currentPage, 0, - this.getWidth(), () => {
                this.applyNext();
            });
        }
    },
});
