
















import { Vue, Component } from 'vue-property-decorator';
import BackHeader from '@/components/BackHeader.vue';
import BookItem from '@/components/BookItem.vue';
import { InfiniteScroll } from 'mint-ui';
import { IBook, getBookList } from '@/api/book';

Vue.use(InfiniteScroll);

@Component({
  components: {
    BackHeader,
    BookItem,
  },
})
export default class Bang extends Vue {
    tabs: any[] = [
        {
            name: '',
            key: 'all'
        },
        {
            name: '',
            key: 'month'
        },
        {
            name: '',
            key: 'week'
        }
    ];
    activeTab: number = 0;
    category: number = 0;
    title: string = '';
    items: IBook[] = [];
    has_more = true;
    page = 1;
    is_loading = false;

    created() {
        this.category = parseInt(this.$route.query.category);
        this.title = this.$route.meta.title = this.$route.query.title + '';
        this.refresh();
    }

    tapTab(index: number) {
        this.activeTab = index;
        this.refresh();
    }

    public loadMore() {
        this.goPage( ++ this.page);
    }

    /**
     * refresh
     */
    public refresh() {
        this.items = [];
        this.is_loading = false;
        this.has_more = true;
        this.goPage(this.page = 1);
    }

    public goPage(page: number) {
        if (this.is_loading || !this.has_more) {
            return;
        }
        this.is_loading = true;
        getBookList({
            category: this.category,
            top: this.tabs[this.activeTab].key,
            page,
        }).then(res => {
            this.has_more = res.paging.more;
            this.is_loading = false;
            res.data.forEach((item: IBook) => {
                this.items.push(item);
            });
        })
    }
}
