export function agoFilter(value: number) {
    const now = new Date();
    const diff = Math.floor((now.getTime() - value) / 1000);
    if (diff < 1) {
      return '';
    }
    if (diff < 60) {
      return diff + '';
    }
    if (diff < 3600) {
      return Math.floor(diff / 60) + '';
    }
    if (diff < 86400) {
      return Math.floor(diff / 3600) + '';
    }
    if (diff < 2592000) {
      return Math.floor(diff / 86400) + '';
    }
    const timeDate = new Date(value);
    if (timeDate.getFullYear() === now.getFullYear()) {
      return timeDate.getMonth() + 1 + '' + timeDate.getDate();
    }
    return timeDate.getFullYear() + '' + (timeDate.getMonth() + 1) + '';
}
