





















import { Vue, Component, Prop } from 'vue-property-decorator';
import { IChapter, getChapters } from '../api/book';
import { dispatchChapters } from '@/store/dispatches';

@Component
export default class SliderMenu extends Vue {
    items?: IChapter[] = [];
    isSort: boolean = false;
    @Prop(Number) readonly chapter?: number;


    refresh(book: number) {
        dispatchChapters(book).then(res => {
            this.items = res;
        });
    }

    tapChapter(item: IChapter) {
        this.$emit('read', item);
    }

    tapSort() {
        this.isSort = !this.isSort;
        if (this.items) {
            this.items.reverse();
        }
    }
}
